* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body, html {
	width: 100vw;
	height: 100vh;
  font-family: sans-serif;
  overflow: hidden;
}

a {
  color: inherit; 
  text-decoration: none !important; 
  border: none !important;
}


