i {
  margin: 20px;
}

.area {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.heading {
  font-size: 50px;
}

.white {
  color: white;
}

.background {
  width: 100vw;
  height: 100vh;
  background: -webkit-linear-gradient(205deg, #f87089, #ff8a3d, #ff967e, #ff585d);
  background: -o-linear-gradient(205deg, #f87089, #ff8a3d, #ff967e, #ff585d);
  background: linear-gradient(245deg, #f87089, #ff8a3d, #ff967e, #ff585d);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}



